<template>
  <div class="my-nav">
      <div class="my-nav-content">
          <slot></slot>
      </div>
      <div class="my-nav-cursor" :style="{left:cursorPosition+'px'}"></div>

  </div>
</template>

<script>
export default {
    name:'my-nav',
    props:{
        cursorPosition:{
            type: Number,
            default: 0
        }
    }
}
</script>

<style>
.my-nav{
    background-color: rgb(50, 50, 50);
    height: 42px;
    position: absolute;
    top:50px;
    left:0;
    right:0;
    line-height: 40px;
    color: rgb(218, 218, 218)
}
.my-nav-content > div {
    display: inline-block;
    text-align: center;
    padding: 0 5px;
}
.my-nav-cursor{
    width: 40px;
    height: 0;
    border:1px solid red;
    position: relative;
}

</style>