<template>
  <li class="my-post pure-g">
    <div class="pure-u-1-12"></div>
    <div class="pure-u-11-12">
      <a :href="titleHref" :class="{'disable-link':hasHref}">
        <h4 class="my-post-title">
          <slot name="title"></slot>
        </h4>
      </a>
      <!-- <a href="" class="disable-link">test</a> -->
      <p class="my-post-abstract">
        <slot name="abstract"></slot>
      </p>
      <p class="my-post-date">
        <slot name="date"></slot>
      </p>
    </div>
  </li>
</template>

<script>
export default {
  name: "my-post",
  props: {
    titleHref: {
      type: String,
      default: ""
    }
  },
  computed: {
    hasHref() {
      return this.titleHref == "";
    }
  }
};
</script>

<style>
.my-post {
  position: relative;
  width: 100%;
  margin: 25px 0;
  background: #ffffff;
  border: 1px solid #f0f0f0;
  word-wrap: break-word;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.my-post a {
  text-decoration: none;
  color: black;
}
.my-post a:hover {
  text-decoration: underline;
}
.my-post-title{
    font-size: 12px;
}
.my-post-abstract {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.75);
  line-height: 1.5;
}
.my-post-date {
  font-size: 10px;
  color: rgba(0, 0, 0, 0.5);
}
</style>