<template>
  <div id="app">
    <div class="container clearfix">
      <my-header></my-header>
      <my-nav :cursorPosition="NavCursorPosition">
        <div class="clickable" @click="scrollToWebApps">WebApps/Games</div>
        <div class="clickable" @click="scrollToBlog">Blog</div>
      </my-nav>
      <div class="my-content" id="scrollWrapper" v-on:scroll="handleScroll($event)">
        <div class="my-content-swipe-track pure-g">
          <div class="webapps pure-u-1-2">
            <app-thumbnail :playHref="urls.happySynPlay" :aboutHref="urls.happySynAbout">
              <template v-slot:cover>
                <img src="./assets/img/keyboard_thumb2.png" alt />
              </template>
              <template v-slot:title>Happy Synthsizer</template>
              <template v-slot:description>Draw a wave and play it!</template>
            </app-thumbnail>
            <app-thumbnail :playHref="urls.game2048Play" :aboutHref="urls.game2048About">
              <template v-slot:cover>
                <img src="./assets/img/2048-3d.png" alt />
              </template>
              <template v-slot:title>2048 - 3D</template>
              <template v-slot:description>3D version of the 2048 game.</template>
            </app-thumbnail>
          </div>
          <div class="blogs pure-u-1-2">
            <ul class="my-post-list">
              <my-post v-for="post in articleList" :key="post.title" :titleHref="post.titleHref">
                <template v-slot:title>{{post.title}}</template>
                <template v-slot:abstract>{{post.abstract}}</template>
                <template v-slot:date>{{post.data}}</template>
              </my-post>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import "./assets/css/base.css";
import "./assets/css/style.css";

import axios from "axios";
import { urls } from "./settings";

import MyHeader from "./components/MyHeader";
import MyNav from "./components/MyNav";
import AppThumbnail from "./components/AppThumbnail";
import MyPost from "./components/MyPost";

export default {
  name: "app",
  components: {
    MyHeader,
    MyNav,
    AppThumbnail,
    MyPost
  },
  data() {
    return {
      scrollSwitch: 0,
      scrollCounter: 0,
      NavCursorPosition: 60,
      NavCursorPositionList: [60, 140],
      articleList: [],
      urls
    };
  },
  created() {
    axios.get(urls.articleList).then(res => {
      this.articleList = res.data;
      //console.log(res);
    });
  },
  methods: {
    handleScroll(ev) {
      this.NavCursorPosition =
        this.NavCursorPositionList[0] +
        ((this.NavCursorPositionList[1] - this.NavCursorPositionList[0]) *
          ev.target.scrollLeft) /
          Math.min(768, window.innerWidth);
      if (ev.target.scrollLeft > Math.min(768, window.innerWidth) / 2) {
        this.scrollSwitch = 1;
      } else {
        this.scrollSwitch = 0;
      }
      this.scrollCounter += 1;
      setTimeout(() => {
        this.scrollCounter -= 1;
        if (this.scrollCounter === 0) {
          //console.log("scroll end");
          //console.log(this.scrollSwitch);
          ev.target.scrollTo({
            left: this.scrollSwitch * Math.min(768, window.innerWidth),
            behavior: "smooth"
          });
        }
      }, 100);
    },
    scrollToWebApps() {
      document
        .getElementById("scrollWrapper")
        .scrollTo({ left: 0, behavior: "smooth" });
    },
    scrollToBlog() {
      document
        .getElementById("scrollWrapper")
        .scrollTo({
          left: Math.min(768, window.innerWidth),
          behavior: "smooth"
        });
    }
  }
};
</script>


